*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  user-select: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: var(--text);
}

::placeholder {
  opacity: .6;
  transition: opacity .2s;
}

:focus::placeholder {
  opacity: 0;
}

button.ui-button {
  padding: 3px 14px;
  color: var(--button-color);
  background: var(--button);
  border: 1px solid var(--button-border);
  background-clip: border-box;
  font-size: 13px;
  border-radius: 5px;
}

button.ui-button:focus {
  color: #fff;
  background: var(--accent);
  background-image: linear-gradient(var(--accent-light), var(--accent));
  border: 1px solid var(--accent);
  outline: none;
}

button.ui-button:active {
  color: #fff;
  background: var(--accent-dark);
  background-image: linear-gradient(var(--accent), var(--accent-dark));
  border: 1px solid var(--accent-dark);
}

button.ui-button:disabled {
  opacity: .5;
}

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

#frame {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: left .25s ease-out;
}

#frame.slide {
  left: 200px;
}

footer {
  width: 200px;
  border-right: 1px solid var(--dividers);
  position: absolute;
  top: 0;
  left: -200px;
  bottom: 0;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: left .25s ease-out;
}

footer.slide {
  left: 0;
}

footer #cat-title {
  display: block;
  font-size: 12px;
  font-weight: 500;
  color: var(--text-light);
  padding: 10px;
}

footer .scroll {
  overflow-x: hidden;
  overflow-y: scroll;
  flex-grow: 2;
}

footer ul {
  display: flex;
  flex-direction: column;
}

footer ul:first-of-type {
  margin-bottom: 16px;
}

footer .search-field {
  height: 40px;
  padding: 8px 10px;
  position: relative;
}

footer .search-field #search {
  color: var(--text-color);
  width: 100%;
  height: 22px;
  font-size: 13px;
  padding: 0 24px;
  background: #ffffff var(--icon-search) no-repeat left 5px center;
  border: 1px solid #b9b9b9;
  border-radius: 3px;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.05);
}

footer .search-field #search:focus {
  outline: none;
  border-color: var(--accent);
  box-shadow: 0 0 3px 1px var(--accent-light);
}

footer .search-field #clear {
  display: none;
  width: 20px;
  height: 20px;
  background: var(--icon-clear) no-repeat center;
  position: absolute;
  top: 9px;
  right: 11px;
}

footer button {
  border: 0;
  border-radius: 0;
  width: 100%;
  text-align: left;
  color: var(--text);
  background-color: transparent;
  font-size: 14px;
  line-height: 24px;
  transition: background-color .15s;
}

footer button:focus {
  background-color: rgba(0, 0, 0, 0.15);
  outline: none;
}

footer button.selected, footer button.selected:focus {
  color: #fff;
  background-color: var(--accent-light);
}

footer #fixed-categories button {
  padding: 0 10px 0 30px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background-position: left 8px center;
  background-repeat: no-repeat;
}

footer #fixed-categories button#cat-all {
  background-image: var(--all);
}

footer #fixed-categories button#cat-fav {
  background-image: var(--favorite);
}

footer #fixed-categories button#cat-none {
  background-image: var(--none);
  background-position-x: 10px;
}

footer #fixed-categories button.selected:focus#cat-all {
  background-image: var(--all-selected);
}

footer #fixed-categories button.selected:focus#cat-fav {
  background-image: var(--favorite-selected);
}

footer #fixed-categories button.selected:focus#cat-none {
  background-image: var(--none-selected);
}

footer #categories.asc {
  flex-direction: column;
}

footer #categories.desc {
  flex-direction: column-reverse;
}

footer #categories button {
  padding: 0 10px 0 30px;
  background-image: var(--category);
  background-position: left 8px center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: row;
}

footer #categories button .cat-name {
  flex-grow: 2;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: 5px;
}

footer #categories button.selected:focus {
  background-image: var(--category-selected);
}

footer #categories button .cat-count {
  opacity: .5;
  display: none;
}

footer #categories button .cat-count.show {
  display: block;
}

main {
  margin-left: 200px;
  padding: 0;
  height: 100vh;
  font-size: 16px;
  line-height: 1.4;
  overflow-y: scroll;
  position: relative;
}

main::-webkit-scrollbar {
  width: 0 !important;
}

main #note {
  border: 0;
  background-color: transparent;
  resize: none;
}

main .loader {
  position: absolute;
  top: 55px;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--background);
  z-index: 1;
}

main .loader .spinner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--loader) no-repeat center center;
}

main #edit {
  position: fixed;
  right: 15px;
  bottom: 15px;
  width: 32px;
  height: 32px;
  background-color: var(--edit);
  background-position: center center;
  background-repeat: no-repeat;
  border: 1px solid var(--text);
  border-radius: 3px;
  background-image: var(--pencil);
  opacity: .5;
  z-index: 2;
  transition: all .15s ease-in-out;
}

main #edit:focus {
  opacity: 1;
  background-color: var(--accent-light);
  border-color: transparent;
  outline: none;
}

main #edit:hover {
  opacity: 1;
  background-color: var(--edit-hover);
  background-image: var(--pencil-white);
  border-color: transparent;
}

main #edit:active {
  background-color: var(--edit-active);
  background-image: var(--pencil-white);
  border-color: transparent;
}

main #edit.editing {
  background-image: var(--eye);
}

main #edit.editing:hover {
  background-image: var(--eye-white);
}

main #edit.editing:active {
  background-image: var(--eye-white);
}

main samp {
  display: block;
  position: absolute;
  left: 1px;
  bottom: 0;
  z-index: 2;
  font-size: 0;
  padding: 2px 12px;
  color: var(--text);
  background-color: var(--window);
  border-top: 1px solid var(--dividers);
  border-right: 1px solid var(--dividers);
  border-radius: 0 3px 0 0;
  max-width: 0;
  opacity: 0;
  transition: all .25s;
}

main samp.show {
  font-size: 11px;
  max-width: calc(100% - 70px);
  opacity: 1;
}

main samp span {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

main #update,
main #dev {
  position: absolute;
  top: -20px;
  right: 0;
  height: 15px;
  font-size: 11px;
  line-height: 11px;
  font-weight: 500;
  color: #fff;
  border: 0;
  border-radius: 0 0 0 3px;
  transition: top .5s;
}

main #update.slidedown,
main #dev.slidedown {
  top: -1px;
}

main #update {
  background-color: #6236FF;
}

main #update:active {
  background-color: #9164FF;
}

main #update:focus {
  outline: none;
}

main #dev {
  padding: 1px 6px;
  background-color: var(--code-green-lt);
}

main #dev.α {
  background-color: var(--code-red-lt);
}

main #dev.β {
  background-color: var(--code-blue-lt);
}

aside {
  width: 200px;
  float: left;
  height: 100vh;
  background: var(--aside);
  border-right: 1px solid var(--dividers);
  position: relative;
  display: flex;
  flex-direction: column;
}

aside #result {
  min-height: 30px;
  font-size: 12px;
  font-weight: 500;
  line-height: 30px;
  color: var(--text-light);
  background: #f6f6f6;
  border-bottom: 1px solid #fff;
  padding: 0 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: none;
}

aside .inner {
  overflow-x: hidden;
  overflow-y: scroll;
}

#divider {
  width: 6px;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  cursor: col-resize;
  z-index: 1000;
}

#sidebar button {
  width: 100%;
  padding: 11px 0;
  display: block;
  border: 0;
  border-radius: 0;
  text-align: left;
  color: var(--text);
  background-color: transparent;
  position: relative;
}

#sidebar button::after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 25px;
  right: 0;
  height: 1px;
  background-color: var(--dividers);
}

#sidebar button span {
  display: block;
  padding-left: 25px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: var(--text);
}

#sidebar button span span {
  display: inline;
  padding-left: 0;
  color: var(--text);
}

#sidebar button .side-title {
  font-size: 13px;
  font-weight: 700;
  padding-bottom: 2px;
  position: relative;
}

#sidebar button .side-text {
  font-size: 12px;
}

#sidebar button .side-text .excerpt {
  color: var(--text-light);
}

#sidebar button .side-text .checked,
#sidebar button .side-text .unchecked {
  appearance: none;
  font-size: 10px;
  width: 10px;
  height: 10px;
  display: inline-block;
  vertical-align: -1px;
  background-color: var(--text-light);
  mask-repeat: no-repeat;
  mask-position: left bottom;
  background-size: cover;
}

#sidebar button .side-text .checked {
  mask: var(--checked);
}

#sidebar button .side-text .unchecked {
  mask: var(--unchecked);
}

#sidebar button .side-cat {
  font-size: 12px;
  padding: 2px 0 0 44px;
  color: var(--text-light);
  background: var(--category) no-repeat left 25px top 4px;
  display: none;
}

#sidebar button[data-favorite="true"] .side-title::before {
  content: '';
  display: block;
  width: 14px;
  height: 14px;
  background: var(--favorite) no-repeat center;
  position: absolute;
  top: 0;
  left: 4px;
}

#sidebar button:focus {
  outline: none;
  background-color: var(--app-color-focus);
}

#sidebar button:focus::after {
  display: none;
}

#sidebar button.selected, #sidebar button.selected:focus {
  outline: none;
  background-color: var(--app-light);
}

#sidebar button.selected::after, #sidebar button.selected:focus::after {
  display: none;
}

#sidebar button.above-selected::after {
  display: none;
}

#sidebar.showcats button .side-cat {
  display: block;
}

#sidebar.hidecats button .side-cat {
  display: none;
}

.EasyMDEContainer .CodeMirror {
  border: none;
  color: inherit;
  background: transparent;
  border-top: 1px solid var(--dividers);
  position: absolute;
  top: 54px;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0 22px;
}

.EasyMDEContainer .CodeMirror .cm-spell-error:not(.cm-url):not(.cm-comment):not(.cm-tag):not(.cm-word) {
  background: none;
  background-image: none;
  background-position: 0 bottom;
  background-repeat: repeat-x;
  background-size: 5px 2px;
}

.EasyMDEContainer .CodeMirror.spellcheck .cm-spell-error:not(.cm-url):not(.cm-comment):not(.cm-tag):not(.cm-word) {
  background-image: linear-gradient(to right, red 50%, transparent 50%);
}

.CodeMirror-code {
  margin-top: 22px;
}

.editor-toolbar {
  background: var(--aside);
  border: 0;
  border-radius: 0;
  height: 54px;
  padding-top: 3px;
  overflow: hidden;
  text-align: center;
  white-space: nowrap;
}

.editor-toolbar button {
  cursor: default;
  font-size: 15px;
  transition: all .3s;
}

.editor-toolbar button i {
  display: block;
  width: 28px;
  height: 28px;
  background-color: var(--text);
  background-repeat: no-repeat;
  background-position: center center;
}

.editor-toolbar button:hover {
  background-color: var(--toolbar-hover);
  border-color: var(--toolbar-border);
}

.editor-toolbar button.active {
  background-color: var(--app-light);
  border-color: var(--app-light);
}

.editor-toolbar button:focus {
  outline: none;
  border-color: var(--accent);
  box-shadow: 0 0 3px 1px var(--accent-light);
}

.editor-toolbar button.icon-heading i {
  mask: var(--icon-heading) no-repeat center center;
}

.editor-toolbar button.icon-b i {
  mask: var(--icon-b) no-repeat center center;
}

.editor-toolbar button.icon-i i {
  mask: var(--icon-i) no-repeat center center;
}

.editor-toolbar button.icon-del i {
  mask: var(--icon-del) no-repeat center center;
}

.editor-toolbar button.icon-ul i {
  mask: var(--icon-ul) no-repeat center top 7px;
}

.editor-toolbar button.icon-ol i {
  mask: var(--icon-ol) no-repeat center center;
}

.editor-toolbar button.icon-checklist i {
  mask: var(--icon-checklist) no-repeat center center;
}

.editor-toolbar button.icon-a i {
  mask: var(--icon-a) no-repeat center center;
}

.editor-toolbar button.icon-img i {
  mask: var(--icon-img) no-repeat center center;
}

.editor-toolbar button.icon-code i {
  mask: var(--icon-code) no-repeat center center;
}

.editor-toolbar button.icon-blockquote i {
  mask: var(--icon-blockquote) no-repeat center center;
}

.editor-toolbar button.icon-table i {
  mask: var(--icon-table) no-repeat center center;
}

.editor-toolbar button.icon-hr i {
  mask: var(--icon-hr) no-repeat center center;
}

.editor-toolbar i.separator {
  border-left-color: var(--dividers);
  border-right-color: #fff;
  vertical-align: 7px;
}

.editor-preview {
  padding: 22px;
  background: var(--background);
}

[data-theme="dark"] #frame {
  background-image: none;
}

[data-theme="dark"] footer {
  border-right: 1px solid var(--header-border);
}

[data-theme="dark"] footer .search-field #search {
  background-color: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.1);
  box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.1);
}

[data-theme="dark"] footer .search-field #search:focus {
  outline: none;
  border-color: var(--accent);
  box-shadow: 0 0 3px 1px var(--accent-light);
}

[data-theme="dark"] footer button:focus {
  background-color: rgba(255, 255, 255, 0.075);
  outline: none;
}

[data-theme="dark"] footer button.selected, [data-theme="dark"] footer button.selected:focus {
  color: #fff;
  background-color: var(--accent-light);
}

[data-theme="dark"] main {
  background-image: var(--background);
}

[data-theme="dark"] main #edit {
  border: 0;
  background-image: var(--pencil-white);
  opacity: .7;
}

[data-theme="dark"] main #edit:focus {
  background-image: var(--pencil);
}

[data-theme="dark"] main #edit:hover {
  background-image: var(--pencil);
}

[data-theme="dark"] main #edit:active {
  opacity: 1;
}

[data-theme="dark"] main #edit.editing {
  background-image: var(--eye-white);
}

[data-theme="dark"] main #edit.editing:focus {
  background-image: var(--eye);
}

[data-theme="dark"] main #edit.editing:hover {
  background-image: var(--eye);
}

[data-theme="dark"] main #edit.editing:active {
  background-image: var(--eye);
}

[data-theme="dark"] aside {
  border-right: 1px solid var(--header-border);
}

[data-theme="dark"] aside #result {
  background: #2e2e2e;
  border-bottom: 1px solid var(--header-border);
}

[data-theme="dark"] #sidebar button.selected, [data-theme="dark"] #sidebar button.selected:focus {
  background-color: var(--app-dark);
}

[data-theme="dark"] #sidebar button.selected span, [data-theme="dark"] #sidebar button.selected:focus span {
  color: white;
}

[data-theme="dark"] #sidebar button.selected .excerpt,
[data-theme="dark"] #sidebar button.selected .side-cat, [data-theme="dark"] #sidebar button.selected:focus .excerpt,
[data-theme="dark"] #sidebar button.selected:focus .side-cat {
  color: var(--text-light);
}

[data-theme="dark"] .editor-toolbar button.active {
  background-color: var(--app-dark);
  border-color: var(--app-dark);
}

[data-theme="dark"] .editor-toolbar i.separator {
  border-left-color: var(--header-border);
  border-right-color: var(--dividers);
}

[data-theme="dark"] .EasyMDEContainer .CodeMirror {
  border-top: 1px solid var(--header-border);
}

[data-theme="dark"] .CodeMirror .CodeMirror-cursor {
  border-left: 1px solid white;
}

[data-user="light"] footer {
  background-color: #edebed;
}

[data-user="dark"] footer {
  background-color: #1e1e1e;
  background: var(--background);
}

[data-user="dark"] aside {
  background-color: #1E1E1E;
}

[data-select] #sidebar button.selected:focus {
  --text: #fff;
  --text-light: rgba(255,255,255,.6);
}

[data-select] #sidebar button.selected:focus[data-favorite=true] .side-title::before {
  background-image: var(--favorite-selected);
}

[data-select] #sidebar button.selected:focus .side-cat {
  background-image: var(--category-selected);
}

[data-select] .editor-toolbar button.active i {
  background-color: #fff;
}

@keyframes pulse {
  0% {
    color: var(--link);
  }
  70% {
    color: var(--hover);
  }
  100% {
    color: var(--link);
  }
}

.CodeMirror-code * {
  font-size: inherit !important;
}

.editor-preview-active {
  user-select: text;
  font-weight: 400;
  width: 100%;
}

.editor-preview-active h1, .editor-preview-active h2, .editor-preview-active h3, .editor-preview-active h4, .editor-preview-active h5, .editor-preview-active h6 {
  line-height: 1;
  margin: 1ex 0;
}

.editor-preview-active h1:first-of-type {
  margin-top: 0;
}

.editor-preview-active p, .editor-preview-active blockquote, .editor-preview-active ul, .editor-preview-active ol, .editor-preview-active pre, .editor-preview-active hr, .editor-preview-active dl {
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}

.editor-preview-active small {
  font-size: .8em;
}

.editor-preview-active sup, .editor-preview-active sub {
  font-size: .6em;
}

.editor-preview-active sup {
  vertical-align: super;
}

.editor-preview-active sub {
  vertical-align: sub;
}

.editor-preview-active table {
  margin-top: 1.5em;
  margin-bottom: 2em;
}

.editor-preview-active table th, .editor-preview-active table td {
  padding: .8em;
}

.editor-preview-active h1, .editor-preview-active h2, .editor-preview-active h3, .editor-preview-active h4, .editor-preview-active h5, .editor-preview-active h6 {
  font-weight: 600;
}

.editor-preview-active b, .editor-preview-active strong {
  font-weight: 700;
}

.editor-preview-active i, .editor-preview-active em {
  font-style: italic;
}

.editor-preview-active h1 {
  font-size: 2.2em;
}

.editor-preview-active h2 {
  font-size: 1.8em;
}

.editor-preview-active h3 {
  font-size: 1.6em;
}

.editor-preview-active h4 {
  font-size: 1.4em;
}

.editor-preview-active h5 {
  font-size: 1.2em;
}

.editor-preview-active h6 {
  font-size: 1.1em;
}

.editor-preview-active blockquote {
  padding-left: 2ch;
  border-left: 0.2em solid var(--app-light);
}

.editor-preview-active ul {
  list-style-type: square;
  margin-left: 2ch;
}

.editor-preview-active ul ul {
  list-style-type: disc;
}

.editor-preview-active ul ul ul {
  list-style-type: circle;
}

.editor-preview-active ol {
  list-style-type: decimal;
  margin-left: 2ch;
}

.editor-preview-active ul ul,
.editor-preview-active ol ol,
.editor-preview-active ol ul,
.editor-preview-active ul ol {
  margin-top: 0;
  margin-bottom: 0;
}

.editor-preview-active li {
  margin-bottom: .2em;
}

.editor-preview-active li input[type="checkbox"] {
  appearance: none;
  font-size: 1em;
  width: 1em;
  height: 1em;
  display: inline-block;
  margin-right: .25em;
  vertical-align: -0.35em;
  background-color: #b8b8b8;
  mask: var(--unchecked);
  mask-repeat: no-repeat;
  mask-position: left bottom;
  background-size: cover;
}

.editor-preview-active li input[type="checkbox"][checked] {
  background-color: var(--app-dark);
  mask: var(--checked);
}

.editor-preview-active dt {
  font-weight: 700;
  margin-top: .8em;
}

.editor-preview-active dd {
  margin-left: 2ch;
  display: list-item;
  list-style-type: disc;
}

.editor-preview-active code {
  color: var(--code-color);
  background-color: var(--code);
  padding-left: 1ch;
  padding-right: 1ch;
  font-family: 'Menlo', monospace;
  font-size: .9em;
  border-radius: 0.3em;
}

.editor-preview-active pre {
  background-color: var(--code);
  padding: 1.5ch 2ch;
  border-radius: 0.4em;
  overflow-x: scroll;
}

.editor-preview-active pre code {
  background-color: transparent;
  padding: 0;
  white-space: pre;
}

.editor-preview-active hr {
  border: 1px solid var(--code);
  border-top: 0;
}

.editor-preview-active a {
  color: var(--link);
  text-decoration: none;
  transition: color .15s;
}

.editor-preview-active a:hover {
