}

.editor-preview-active a:focus {
  outline: 4px solid -webkit-focus-ring-color;
}

.editor-preview-active img {
  display: inline-block;
  max-width: 100%;
}

.editor-preview-active mark {
  color: var(--mark);
  background-color: var(--app-light);
  padding-left: .5ch;
  padding-right: .5ch;
}

.editor-preview-active table thead {
  background-color: var(--table-head);
  font-weight: 700;
}

.editor-preview-active table tbody tr:nth-of-type(even) {
  background-color: var(--table-stripe);
}

.editor-preview-active table th, .editor-preview-active table td {
  border-color: var(--table-border);
}

[data-theme="dark"] .editor-preview-active li input[type="checkbox"][checked] {
  background-color: var(--app-light);
}

[data-theme="dark"] .editor-preview-active blockquote {
  border-left-color: var(--app-dark);
}

[data-theme="dark"] .editor-preview-active mark {
  background-color: var(--app-dark);
